import { getCourseCarryFee, getCourseDetail, getCourseLink } from "@/api/course"
import config from "@/config/common.config"
import qs from "qs"
import WeChatShare from "@/mixins/WeChatShare"
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/swiper-bundle.css'
export default {
  components: {
    Swiper,
    SwiperSlide
  },
  mixins:[WeChatShare],
  data () {
    return {
      carry_fee: 0,
      id: null,
      config,
      query: {},
      allImgIndex: [0],
      width: 340,
      shareLink: null,
      shareOption: {},
      show: false,
      imgShow:true,
      shareShow:false,
      modalData:[
        '1.分享海报后通过扫码进入购买课程即为邀请成功，邀请成功获得一定的佣金；',
        '2.邀请的用户在购买课程时使用了优惠券可能会影响佣金金额；',
        '3.登录艾思云课堂平台即可查看收益明细，链接:',
        '4.本活动的最终解释权为艾思平台所有。'
      ],
      swiperOptions:{
        centeredSlides:true,
        slidesPerView:1.25,
        loop: true,
        //lazy: true,
        on:{
          slideChange:() => {
            const swiper = this.$refs.mySwiper.$swiper
            if(swiper) {
              const index = swiper.realIndex
              if(!this.allImgIndex.includes(index)) {
                this.allImgIndex.push(index)
                //往左切第一张闪烁
                if(index === 2 && this.imgShow) {
                  const timeOut = setTimeout(() =>{
                    this.imgShow = !this.allImgIndex.includes(2)
                    clearTimeout(timeOut)
                  }, 500)
                }
              }
            }
          }
        }
      }
    }
  },
  mounted () {
    this.getHandleDetail()
    this.width = (document.body.clientWidth/375) *350
  },
  methods: {
    async getHandleDetail () {
      this.id = this.$route.params.id
      const { data } = await getCourseCarryFee(this.id)
      if (data.code === 0) {
        this.query = qs.stringify(this.$route.query)
        this.carry_fee = data.data
        const res = await getCourseLink(this.id)
        const inviterId = res.data.data.link.split('=')[1]
        this.shareLink = `${window.location.origin}/courseDetail/${this.$route.params.id}?inviterId=${inviterId}`
        this.setWeChatShare()
      }
    },
    //轮播
    handleChange (index) {
      if(!this.allImgIndex.includes(index)) {
        this.allImgIndex.push(index)
      }
    },
    //设置当前页面的分享
    async setWeChatShare () {
      const { data } = await getCourseDetail(this.$route.params.id)
      if(data.code === 0) {
        this.shareOption = {
          title : data.data.name,
          desc: `我已参加这个课程，你也来一起参加吧!`,
          imgUrl: `${config.IMG_URL.default}${data.data.poster}`,
          link: this.shareLink
        }
        this.getWechatShare() 
      }
    },
    //切换到指定图片
    swiperSlide (index) {
      const swiper = this.$refs.mySwiper.$swiper
      const realIndex = swiper.realIndex
      if ((realIndex === 0 && index ===2) ||(realIndex - index) === 1) {
        swiper.slidePrev()
      } else if ((realIndex === 2 && index === 0) || (realIndex - index) === -1) {
        swiper.slideNext()
      }
    },
    onCopySuccess() {
      this.$toast.success("复制成功")
    },
  },
  head() {
    return {
      title: '邀请好友—艾思云课堂'
    }
  }
}
